body {
	background-color: rgb(250, 250, 250);
}

.container {
	display: flex;
	align-items: center;
	flex-direction: column;
	margin: 1% auto;
}

.image {
	width: 500px;
}

@media only screen and (max-width: 770px) {
	.container {
		margin: 1% 10%;
	}

	.image {
		width: 100%;
	}
}
